.about-me-container {
  position: absolute;
  top: 0;
  left: 1rem;
  width: 40rem;
  height: 100%;
  padding-left: 1rem;
  @media only screen and (max-width: 500px) {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 4rem;
    gap: 3rem;
    padding: 0;
    left: 0rem;
  }
  @media only screen and (min-width: 800px) and (max-width: 1000px) {
    width: 28rem;
    padding-left: 1rem;
  }
  @media only screen and (min-width: 1024px) and (max-width: 1280px) {
    width: 35rem;
    padding-left: 1.5rem;
  }
  @media only screen and (min-width: 1366px) and (max-width: 1440px) {
    width: 35rem;
    padding-left: 1.5rem;
  }
  @media only screen and (min-width: 2304px) and (max-width: 2560px) {
    width: 60rem;
    padding-left: 3rem;
  }
  @media only screen and (min-width: 2880px) {
    width: 70rem;
    padding-left: 3rem;
  }
  @media only screen and (min-width: 3840px) {
    width: 80rem;
    padding-left: 3rem;
  }
  .about-me-title {
    font-size: 4rem;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.9);
    @media only screen and (max-width: 500px) {
      font-size: 2.4rem;
      margin-top: 0rem;
      padding-left: 2.5rem;
    }
    @media only screen and (min-width: 800px) and (max-width: 1000px) {
      font-size: 2rem;
    }
    @media only screen and (min-width: 1024px) and (max-width: 1280px) {
      font-size: 3rem;
    }
    @media only screen and (min-width: 1366px) and (max-width: 1440px) {
      font-size: 3.5rem;
    }
    @media only screen and (min-width: 2304px) and (max-width: 2560px) {
      font-size: 5rem;
    }
    @media only screen and (min-width: 2880px) {
      font-size: 6rem;
    }
    @media only screen and (min-width: 3840px) {
      font-size: 7rem;
    }
  }
  .about-me-text {
    width: 100%;
    height: 85%;
    padding-top: 1rem;
    justify-content: center;
    display: flex;
    flex-direction: column;
    gap: 2rem;

    @media only screen and (max-width: 500px) {
      width: 100%;
      padding: 2rem 3.1rem 0rem 2rem;
      height: auto;
    }
    @media only screen and (min-width: 2880px) {
      padding-top: 5rem;
    }
    .text-info {
      text-indent: 1em;
      color: rgba(255, 255, 255, 0.8);
      font-size: 2rem;
      line-height: 2.5rem;
      @media only screen and (max-width: 500px) {
        font-size: 1.6rem;
        line-height: 2rem;
      }
      @media only screen and (min-width: 800px) and (max-width: 1000px) {
        font-size: 1.2rem;
        line-height: 1.4rem;
      }
      @media only screen and (min-width: 1024px) and (max-width: 1280px) {
        font-size: 1.6rem;
        line-height: 2rem;
      }
      @media only screen and (min-width: 1366px) and (max-width: 1440px) {
        font-size: 1.6rem;
        line-height: 2.4rem;
      }
      @media only screen and (min-width: 2304px) and (max-width: 2560px) {
        font-size: 2.5rem;
        line-height: 4rem;
      }
      @media only screen and (min-width: 2880px) {
        font-size: 3.5rem;
        line-height: 4.5rem;
      }
      @media only screen and (min-width: 3840px) {
        font-size: 4rem;
        line-height: 4.5rem;
      }
    }
  }
  .bottom-line-about-me {
    position: absolute;
    left: -1.5rem;
    bottom: 0;
    height: 0.1rem;
    background-color: white;
    opacity: 85%;
    @media only screen and (max-width: 500px) {
      display: none;
    }
  }
  .about-me-close-button {
    position: absolute;
    bottom: 1rem;
    right: 2rem;
    background-color: transparent;
    border: none;
    color: white;
    font-size: 2rem;
    cursor: pointer;
    @media only screen and (max-width: 500px) {
      display: none;
    }
    @media only screen and (min-width: 2304px) and (max-width: 2560px) {
      bottom: 3rem;
    }
    @media only screen and (min-width: 2880px) {
      bottom: 3rem;
    }
    @media only screen and (min-width: 3840px) {
      bottom: 3rem;
    }
  }
}
