.container-in {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: 800;
  background: transparent;
  z-index: 99;
  .circle-large {
    bottom: -90rem;
    right: -70rem;
    position: absolute;
    width: 150rem;
    height: 150rem;
    z-index: 0;
    transform: scale(0);
    @media only screen and (max-width: 500px) {
      bottom: -77rem;
      right: -74rem;
    }
  }
  .circle-small {
    left: -10rem;
    top: -40rem;
    position: absolute;
    width: 80rem;
    height: 80rem;
    z-index: 0;
    transform: scale(0);
    @media only screen and (max-width: 500px) {
      left: -35rem;
      top: -40rem;
    }
  }
}
.close-contact {
  @media only screen and (max-width: 500px) {
    position: absolute;
    top: 2rem !important;
    right: 2rem !important;
  }
}
.container-in-screen {
  position: relative;
  padding: 0;
  width: 100%;
  height: 100%;
  left: 0;
  will-change: top;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container-in-screen,
.container-in .rounded-div-wrap.top .rounded-div,
.container-in .rounded-div-wrap.bottom .rounded-div {
  background: #141517;
}
.container-in .rounded-div-wrap.top .rounded-div,
.container-in .rounded-div-wrap.bottom .rounded-div {
  height: 745%;
}
.container-in .rounded-div-wrap.top {
  top: 0;
  position: absolute;
  transform: translateY(-99%) scale(1, -1);
  height: 0vh;
  will-change: height;
}

.container-in .rounded-div-wrap.bottom {
  top: unset;
  bottom: 0;
  position: absolute;
  transform: translateY(99%);
  z-index: 2;
  height: 0vh;
  will-change: height;
}

.rounded-div-wrap {
  width: 100%;
  top: 0;
  position: relative;
  height: 10vh;
  overflow: hidden;
}

.rounded-div {
  width: 150%;
  content: "";
  display: block;
  position: absolute;
  background: white;
  height: 750%;
  left: 50%;
  border-radius: 50%;
  transform: translate(-50%, -86.666%);
  z-index: 1;
}
